.picture-grid img {
  width: 25vw;
}

.picture-grid-two img {
  width: 40vw;
}

.picture-grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-row-gap: 10px;
  width: 79vw;
}

.picture-grid-two {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-row-gap: 10px;
  grid-column-gap: 10px;
  width: 79vw;
}

.container {
  width: 100vw;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

p {
  margin-bottom: 4vh;
}

.reference a,
.reference h3 {
  text-decoration: none;
  color: #535d63;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.link {
    text-decoration: none;
    color: #535d63;
    z-index: 2;
    font-weight: bold;
    border-radius: 5px;
    text-align: center;
    padding: 0 1em;
    font-size: 1.25em;
}

.link:hover {
    color: white;
    background: #8d9397;
    -webkit-transition: font-size .1s ease-in;
    transition: font-size .1s ease-in;
    cursor: pointer;
}

.link:hover a {
    color: white;
}

.link:hover li {
    color: white;
}
.droplinks {
    text-decoration: none;
    text-align: start;
    color: #535d63;
    text-transform: none;
}

.droplinks:hover {
    text-decoration: underline;
}

.droplink-head {
    list-style-type: none;
    text-decoration: none;
    color: #535d63;
    text-align: start;
}



.navBar {
  position: absolute;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  width: 40vw;
  z-index: 2;
  -webkit-align-self: flex-end;
          align-self: flex-end;
  top: 15vh;
  right: 3vw;
}

.hamburger-container {
  position: fixed;
  top: 1vh;
  right: 1vw;
  cursor: pointer;
  width: 20vw;
  z-index: 200;
}

.bar1,
.bar2,
.bar3 {
  width: 35px;
  height: 5px;
  margin: 6px 0;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.color-standard {
  background-color: #6c757a;
}

.color-white {
  background-color: white;
}

.change .bar1 {
  -webkit-transform: rotate(-45deg) translate(-9px, 6px);
  transform: rotate(-45deg) translate(-9px, 6px);
}

.change .bar2 {
  opacity: 0;
}

.change .bar3 {
  -webkit-transform: rotate(45deg) translate(-8px, -8px);
  transform: rotate(45deg) translate(-8px, -8px);
}

.drop-down-container {
  background: #535d63;
  width: 60vw;
  height: 100vh;
  z-index: 100;
  position: fixed;
  top: 0;
  right: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: flex-end;
          align-items: flex-end;
}

